body {
  background-color: #e16102;
}

.App {
  text-align: center;
}

.App-logo {
  /* height: 40vmin; */
  pointer-events: none;
}

.App-header {
  min-height: 50vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;

}

.App-link {
  color: #61dafb;
}

.splash {
  margin-top: 8vh;
  margin-bottom: 12vh;
  font-size: calc(16pt + 2vmin);
}

.launch-splash {
  margin-top: 8vh;
}

.signup-form {
  padding: 12vh;
}

section {
  box-sizing: border-box;
  min-height: 50vh;
}

.bgtext {
  /* position: absolute;
  top: 50%;
  left: 50%; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  background-color: white;
}

.bgimage {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* height: 100%; */
  min-height: 80vh;
  background-image: url("https://images.unsplash.com/photo-1556740738-b6a63e27c4df?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1050&q=80");

  /* Add the blur effect */
  /* filter: blur(8px); */
  /* -webkit-filter: blur(8px); */

  /* Full height */
  height: 100%;

  /* Center and scale the image nicely */
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

footer {
  padding: 12vh;
  color: white;
}

footer > a {
  color: white;
}